.main_card_container {
  width: 93vw;
  height: 4vw;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  position: relative;
  color: #fff;
  font-family: "Encode Sans";

}
.mainHeaderContainer{
  background-color: #7839C5;
}
.card_heading {
  color: #ffffff;
  padding: 1.5vw;
  font-size: 1.1vw;
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  margin-right: 4%;
}

.modal-dialog {
  box-shadow: 5px 10px 21.8px 0.7px #9d9d9d80;
}

.heading {
  margin-top: 1%;
  font-size: 1.4vw;
  font-family: "Encode Sans";
  font-weight: 500;
  margin-left: 5%;
  margin-right: 5%;
}

.ViewDocumentButton:hover{
  cursor: pointer;
}

.card_button {
  height: 2em;
  width: 8em;
  background-color: #0089ED;
  border-radius: 5px;
  color: #000;
  font-weight: bold;
  padding: 4px;
  font-size: 1vw;
  font-family: "Encode Sans";
  font-weight: bold;
}

.card_button {
  display: block;
  height: 2em;
  width: 2em;
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

.content {
  margin: 5px;
}

.content {
  font-family: "Encode Sans";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;

  /* identical to box height */
  letter-spacing: 0.02em;

  color: #727272;
}

p {
  font-size: 15px;
}

#btnHeading {
  margin-left: 5%;
  margin-right: 5%;
}

.valueContainer {
  width: 9vw;
  display: flex;
  justify-content: space-between;
  margin-right: 2%;
  margin-left: 3%;
}

.rounded-circle {
  width: 15px;
  height: 15px;
  margin-left: 1%;
}

@media only screen and (max-width: 1030px) {
  .main_card_container {
