  .main_card_container {
    justify-content: space-evenly;
  }

  .card_heading {
    font-size: 2.5vw;
  }

  .card_button img {
    height: 1.3em;
  }

  .heading {
    font-size: 2.2vw;
    padding-top: 1%;
  }

  .rounded-circle {
    width: 5px !important;
    height: 5px !important;
    margin-left: -5% !important;
  }

  .valueContainer {
    width: 1%;
    margin-left: 0%;
  }

  .card_heading {
    margin-left: 1%;
    /* font-size: 70%; */
    inline-size: 40px;
    overflow-wrap: break-word;
  }

  .link-primary_linkViewDocument {
    font-size: 30%;
  }
}

#MainBodyContainer div::-webkit-scrollbar {
  width: 0.7em;
}

#MainBodyContainer div::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(78, 122, 227, 0.4);
  border-radius: 20px;
}

#MainBodyContainer div::-webkit-scrollbar-thumb {
  background-color: rgba(78, 122, 227, 0.94);
  border-radius: 20px;
  outline: 1px solid slategrey;
}
.loader {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: #fff;
    box-shadow: 32px 0 #fff, -32px 0 #fff;
    position: relative;
    -webkit-animation: flash 0.5s ease-out infinite alternate;
            animation: flash 0.5s ease-out infinite alternate;
  }
  
  @-webkit-keyframes flash {
    0% {
      background-color: #FFF2;
      box-shadow: 32px 0 #FFF2, -32px 0 #FFF;
    }
    50% {
      background-color: #FFF;
      box-shadow: 32px 0 #FFF2, -32px 0 #FFF2;
    }
    100% {
      background-color: #FFF2;
      box-shadow: 32px 0 #FFF, -32px 0 #FFF2;
    }
  }
  
  @keyframes flash {
    0% {
      background-color: #FFF2;
      box-shadow: 32px 0 #FFF2, -32px 0 #FFF;
    }
    50% {
      background-color: #FFF;
      box-shadow: 32px 0 #FFF2, -32px 0 #FFF2;
    }
    100% {
      background-color: #FFF2;
      box-shadow: 32px 0 #FFF, -32px 0 #FFF2;
    }
  }
        
#mainDashBoardContainer {
  /* background-color: #ebedf0; */
  background-image: url(/Verification-module/static/media/mit.77077e00.png);
  background-size: cover;
  position: absolute;
