  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Encode Sans";
}

#DashBoard {
  display: flex;
  flex-direction: row;
  width: 80%;
  justify-content: center;
  margin-top: 5vw;
  padding: 2vw;
  background-color: white;
  border-radius: 20px;
  background: rgba(217, 217, 217, 0.0);
  -webkit-backdrop-filter: blur(83.6267px);
          backdrop-filter: blur(83.6267px);
}

.headingDashboard {
  margin: 0;
  font-family: "Encode Sans";
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 34px;
  color: #ffffff;

}

.numbersDashboard {
  margin: 0;
  font-family: "Encode Sans";
  font-style: normal;
  font-weight: 600;
  font-size: 50px;
  line-height: 75px;
  color: #ffffff;
}

.DashBoardContainers {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 20vw;
  height: 18vh;
  justify-content: center;

}

.Admin-flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#Admin-Info-container {
  width: 98%;
  height: 4%;
  margin-top: 2%;
  padding: 1%;
  justify-content: space-between;
  background: #F3F3F5;
  border-radius: 16px 16px 0px 0px;

}

.Admin-Option-details-container {
  width: 50%;
  grid-gap: 30%;
  gap: 30%;
  padding-top: 1%;
  justify-content: flex-start;
}

.Option-detail {
  grid-gap: 5%;
  gap: 5%;
  width: 20%;
}

.Info-property {
  font-size: large;
  font-Weight: 600;
  color: #023473;
}

.Info-value {
  font-size: large;
  color: #787878;
  font-weight: 500;
}

.Admin-Logout-btn-container {
  width: 50%;
  justify-content: end;
}

.Admin-Logout-btn {
  border: none;
  padding: 1% 5%;
  background: #316EBC;
  box-shadow: 4px 4px 19px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  color: #ffffff;
}

.ChildContainer {
  display: flex;
  flex-direction: column;
  margin: 20px;
}

#Main_Item_Container_admin {
  display: flex;
  width: 98%;
  flex-wrap: wrap;
  justify-content: center;
  padding: 1%;
  /* margin-top: -0.%; */
  border-radius: 0 0 6px 6px;
  border-left: solid #ffffff;
  border-right: solid #ffffff;
  border-bottom: solid #ffffff;
  background: rgba(217, 217, 217, 0.0);
  -webkit-backdrop-filter: blur(83.6267px);
          backdrop-filter: blur(83.6267px);
  margin-bottom: 2%;
}

