.main {
    margin: 0%;
    padding: 0%;
    font-family: 'Encode Sans';
    position: relative;
    overflow-x: hidden;
}

.form {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow-x: hidden;
    background-color: white;
    font-family: 'Encode Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    color: #6F6F6F;
}


/* PROGRESS BAR */
.progressbar-container {
    margin-top: 85px;
    width: 30%;
    max-width: 484px;
    min-width: 12rem;
    font-weight: 600;
    font-size: 18px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.progressbar-title {
    display: flex;
    justify-content: space-around;
}

.progressbar {
    display: flex;
    width: 100%;
    height: 5px;
    background-color: #E8EDFF;
    margin-bottom: 25px;
    border-radius: 10px;
}

.progress-form {
    width: 35.5%;
    height: 100%;
    border-radius: 10px;
}

.progress-payment {
    width: 64.5%;
    height: 100%;
    border-radius: 10px;
}

.progressbar div:hover {
    cursor: pointer;
}

.form-container {
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form-body {
    width: 60%;
}

.label {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
}

.form-radios input {
    margin-right: 0;
}

div.form-radios {
    margin-top: 0;
    margin-bottom: 0;
}

#l2 {
    margin-top: 1em;
}

.Ageny-Details-container,
.Candidate-Info-container,
.document-container {
    height: 100%;
    width: 100%;
    margin-right: 5em;
    display: flex;
    flex-direction: column;
    font-size: 15px;
    font-weight: 500;
}


.Ageny-Details-container input,
.Candidate-Info-container input,
.document-container input {
    width: 100%;
    height: 2.5em;
    padding-left: 5px;
    font-size: 15px;
    margin-bottom: 1.5rem;
}


input {
    background: #F7FAFD;
    border: 2px solid #E1E1E1;
    border-radius: 10px;
    color: #6F6F6F;
}

input:focus {
    border: 3px solid #0089ED;
}

input[type=radio],
input[type=checkbox] {
    width: 5%;
    height: 14px;
}

input[name="university-radio"] {
    accent-color: #FE724C;

}

.form-radios label,
.form-checkboxes label {
    font-weight: 500;
}

input[type=file] {
    display: none;
}

.payementSS {
    display: flex;
    justify-content: space-between;
}

.payement-image-btn,
.document-upload-btn {
    border: 2px solid #6c5ce7;
    height: 3em;
    background: #3A71CE;
    border-radius: 6px;
    line-height: 3em;
    text-align: center;
    padding: 0em 1.4em;
    color: white;
}

.form-radios,
.form-checkboxes {
    display: flex;
    align-self: normal;
}

div.form-checkboxes {
    margin-top: 0;
    margin-bottom: 0;
}
.cb-container{
    width: 90%;
}
.verification-info {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.charges-details {
    width: 70%;
    padding: 20px;
    background: #F7FAFD;
    border: 2px solid #E1E1E1;
    border-radius: 10px;
    color: #6F6F6F;
}

.document-upload-container {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    margin-top: 1.5rem;
    width: 100%;
    background: #F7FAFD;
    border: 2px solid #E1E1E1;
    border-radius: 10px;
    color: #6F6F6F;
}

.payimg-container {
    width: 10%;
    position: relative;
    right: -3em;
}

.payimg-container img {
    width: 500%;
    height: 500%;
}

.doc-flex img{
    width: 300%;
}

.form-submit-btn{
    border: 1px solid #6c5ce7;
    height: 2em;
    font-size: 1.4em;
    background: #3A71CE;
    border-radius: 5px;
    line-height: 2em;
    text-align: center;
    padding: 0em 2.4em;
    width: 12em;
    margin-top: 1em;
    align-self: center;
    color: white;
    font-family: 'Encode Sans';
}
.form-container .pageChange {
    display: flex;
    justify-content: center;
    margin-top: 30px;
    margin-bottom: 30px;
}

.pageChange-btn1,
.pageChange-btn2 {
    border-radius: 7px;
    width: 3.5em;
    height: 40px;
    background-color: white;
    color: #0089ED;
    border: 2px solid #0089ED;
    font-weight: bold;
    border-radius: 1px;
    cursor: pointer;
    padding: 10px 5px;
    margin: 5px;
}

.form-container .pageChange button:focus {
    background-color: #0089ED;
    color: white;
    border: 2px solid #6F6F6F;
}

.focus {
    background-color: #0089ED;
    color: white;
    border: 2px solid #6F6F6F;
}
.invalid{
    color:red;
}
