        height: 14px;
    }

    .doc-flex img{
        display: none;
    }
}

@media (max-width:700px) {
    .progressbar-container {
        font-size: 95%;
    }

    .Ageny-Details-container input,
    .Candidate-Info-container input {
        width: 90%;
        height: 2em;
        padding-left: 5px;
        font-size: 15px;
    }

    .document-container {
        width: 80%;
        margin: 1em -5em 0 0;
    }

    input[type=radio],
    input[type=checkbox] {
        margin: 2%;
        width: 6%;
        height: 16px;
    }
    .doc-flex img,.payimg-container{
        display: none;
    }
    .payementSS {
        flex-direction: column;
    }

    .document-upload-container{
        flex-direction: column;
    }

    .verification-info {
        flex-direction: column;
        align-items: flex-start;
    }

    .charges-details {
        width: 100%;
        overflow-x: auto;
        overflow-y: hidden;
        white-space: nowrap;
    }

    .form-radios label,
    .form-checkboxes label {
        padding: 5px;
        font-size: 12px;
    }
    
    .form-submit-btn {
        padding: 0em 1.4em;
        width: 8em;
        margin: 1em 0 0 2em;
    }
}
